<template>
  <div class="terminal">
    <div class="toolbar">
      <div class="icons">
        <div  class="icon close"></div>
        <span class="icon minimize"></span>
        <span class="icon maximize"></span>
      </div>
      <div class="title">
        <p>{{ label }}: ~</p>
      </div>
    </div>
    <div class="body" @keydown.enter="scrollToBottom" ref="terminal">
      <Output v-for="command in this.commands" :command="command" :key="command" @clear="clearTerminal"/>
      <Input @commands="setCommands"/>
    </div>
  </div>
</template>

<script>
import Input from "@/components/Input";
import Output from "@/components/Output";
import config from "@/config/config"

export default {
  name: "Terminal",
  components: {
    Input,
    Output
  },
  data() {
    return {
      commands: [],
      label: config.LABEL,
    }
  },
  methods: {
    setCommands(o) {
      this.commands.push(o);
    },
    clearTerminal() {
      this.commands = []
    },
    scrollToBottom() {
      const terminal = this.$refs.terminal;
      terminal.scrollTop = terminal.scrollHeight;
    }
  }
}
</script>

<style scoped>
  .terminal {
    background-color: rgb(32,32,32);
    width: 900px;
    height: 500px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  }

  @media all and (max-width: 900px) {
    .terminal {
      width: 100%;
      height: 100%;
    }
  }

  .toolbar {
    width: 100%;
    padding-left: 10px;
    box-sizing: border-box;
    display: flex;
  }

  .icon {
    width: 16px;
    height: 16px;
    display: inline-block;
    border-radius: 50%;
    margin: 15px 4px 10px 4px;
    box-sizing: border-box;
  }

  .close {
    background-color: red;
  }

  .maximize {
    background-color: orange;
  }

  .minimize {
    background-color: mediumseagreen;
  }

  .title {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size:  12px;
    flex-basis: 75%;
  }

  .body {
    padding: 15px 20px 15px 20px;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    overflow: auto;
    height: calc(100% - 46px);
  }

  ::-webkit-scrollbar {
    width: 10px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: rgb(32,32,32);
    border-radius: 10px;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #1f7a6b;
    border-radius: 10px;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #1f7a6b;
  }
</style>